@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
:root {
  --base-black: #18181f;
  --base-white: #e5e5ec;
  --white-10: #c9c9d0;
  --transition-time: 0.1s;
}

body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #18181f;
  background-color: var(--base-black);
  color: #e5e5ec;
  color: var(--base-white);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    max-width: 800px;
    margin: auto;
    padding: 0 3rem;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
}

.Info {
    font-size: 0.8rem;
    color: var(--white-10);
    display: flex;
    width: 100%;
    text-align: left;
    justify-content: center;
}

.InfoIcon {
    /* https://codepen.io/sosuke/pen/Pjoqqp */
    -webkit-filter: invert(93%) sepia(6%) saturate(182%) hue-rotate(202deg) brightness(86%) contrast(91%);
            filter: invert(93%) sepia(6%) saturate(182%) hue-rotate(202deg) brightness(86%) contrast(91%);
    margin: 1rem 1rem 1rem 0;
}

.GitHub {
    display: flex;
    cursor: pointer;
    color: var(--white-10);
    text-decoration: underline;
}

.GitHubIcon {
    -webkit-filter: invert(93%) sepia(6%) saturate(182%) hue-rotate(202deg) brightness(86%) contrast(91%);
            filter: invert(93%) sepia(6%) saturate(182%) hue-rotate(202deg) brightness(86%) contrast(91%);
    margin: 0 1rem 0 0;
    height: 1.5rem;
}

.Spacer {
    height: 4rem;
}

.DropZone {
    background-color: rgba(0, 0, 0, 0.25);
    width: 100%;
    height: 450px;
    border-radius: 3px;
    position: relative;
}

.LoadingContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: calc(100% - 4rem);
    height: 100%;
    opacity: 0;
    position: absolute;
    padding: 0 2rem;
    transition: all var(--transition-time);
}

.DropZone.Loading > .LoadingContainer {
    opacity: 1;
}

.DropCallToAction {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    position: absolute;
    transition: all var(--transition-time);
    z-index: 100;
}

.DropZone.Loading > .DropCallToAction, .DropZone.Done > .DropCallToAction {
    opacity: 0;
    pointer-events: none;
    z-index: auto;
    z-index: initial;
}

.ResizedContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    transition: all var(--transition-time);
    position: absolute;
    opacity: 0;
    pointer-events: none;
}

.DropZone.Done > .ResizedContainer {
    opacity: 1;
    pointer-events: initial;
}

.ResizedRow {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 2rem;
    width: 100%;
    margin-bottom: 1rem;
}

.ResizedRow > div {
    text-align: center;
    align-self: end;
}

.ImageSize {
    color: var(--white-10);
    font-size: 0.8rem;
}

.SourceImage {
    display: none;
}

.Button {
    background-color: rgba(255, 255, 255, 0.15);
    padding: 0.5rem 1rem;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 3px;
    transition: all var(--transition-time);
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.2);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Button:hover {
    background-color: rgba(255, 255, 255, 0.25);
}

.Button:active {
    transform: scale(0.96);
}

.ProgressBar {
    background-color: rgba(255, 255, 255, 0.15);
    border-radius: 3px;
    transition: all var(--transition-time);
    width: 100%;
    height: 1rem;
    overflow: hidden;
}

.ProgressBarInner {
    background-color: var(--white-10);
    border-radius: 3px;
    transition: all 0.2s;
    height: 100%;
}

